



















































































































































import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue"


//高德SDK
import amap from '../../common/SDK/amap-wx.js';
export default {

	components: {
		uniLoadMore
	},
	mounted() {


		// 轮播,热销
		uni.request({
			url: 'http://shanpei.wsstreet.net/index', //仅为示例，并非真实接口地址。
			data: {
				token: this.token
			},
			method: "post",
			success: (res) => {
				console.log("hoem", res);
				this.swiperList = res.data.data.banner;
				this.categoryList = res.data.data.cate;
				this.hotList = res.data.data.hot;
				this.limitList = res.data.data.limit_buy;
			}
		})

		//系统消息
		uni.request({
			url: this.config.url + "member/message",
			method: "POST",
			data: {
				token: this.token
			},
			success: (res) => {
				//console.log("mesg",res)
				if (res.data.data.data.length > 0) {
					this.msg = true;
				}
				if (res.data.code == 1) {
					this.msgList = res.data.data.data
				}
			}
		})
	},

	data() {
		return {
			msg: "", //系统消息显示
			msgList: [], //系统信息列表
			showIcon: false,
			status: "more",

			afterHeaderOpacity: 1, //不透明度
			headerPosition: 'fixed',
			headerTop: null,
			statusTop: null,
			city: '北京',
			currentSwiper: 0,
			// 轮播图片
			swiperList: [],
			// 分类菜单
			categoryList: [],
			Promotion: [],
			// 热销产品
			hotList: [],
			//限制产品
			limitList: [],
			//猜你喜欢列表
			productList: [],
			totalList: [],
			loadingText: '正在加载...',

			current_page: "",
			total: "",
			last_page: "",

			current_page: 0,
			total: "",
			last_page: "1",

			tishi: false
		}
	},
	onPageScroll(e) {
		//兼容iOS端下拉时顶部漂移
		this.headerPosition = e.scrollTop >= 0 ? "fixed" : "absolute";
		this.headerTop = e.scrollTop >= 0 ? null : 0;
		this.statusTop = e.scrollTop >= 0 ? null : -this.statusHeight + 'px';
	},
	//下拉刷新，需要自己在page.json文件中配置开启页面下拉刷新 "enablePullDownRefresh": true
	onPullDownRefresh() {
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);
	},
	//上拉加载，需要自己在page.json文件中配置"onReachBottomDistance"
	onReachBottom() {


		this.count++
		console.log(count)

		// 调用获取推荐列表接口
		// 当前页小于最后一页才调用
		if (this.current_page < this.last_page) {
			this.getRecommendList();
		}

	},
	onLoad() {
	
		var amapPlugin = new amap.AMapWX({
			//高德地图KEY，随时失效，请务必替换为自己的KEY，参考：http://ask.dcloud.net.cn/article/35070
			key: '5b9b64be2413fc19c26683fcf0de890f'
		});
		//定位地址
		amapPlugin.getRegeo({
			success: data => {
				//console.log(data)
				this.city = data[0].regeocodeData.addressComponent.city.replace(/市/g, ''); //把"市"去掉
			}
		});


	},
	methods: {
		// 获取推荐列表
		getRecommendList() {
			this.status = "loading";
			// 首页为你推荐
			uni.request({
				url: 'http://shanpei.wsstreet.net/recommend', //仅为示例，并非真实接口地址。
				data: {
					token: this.token,
					page: Number(this.current_page) + 1,
				},
				method: "post",
				success: (res) => {
					var len;
					console.log("res.data", res.data);
					// 商品列表
					this.totalList = res.data.data.data;
					//每页10 
					if (this.totalList.length < 10) {
						len = this.totalList.length;
					} else {
						len = 10;
					}
					for (var i = 0; i < len; i++) {
						this.productList.push(this.totalList[i])
						//	console.log(this.productList)
					}

					this.current_page = res.data.data.current_page;
					this.last_page = res.data.data.last_page;
					this.total = res.data.data.data.total;
					this.status = "more";
					if (this.current_page >= this.last_page) {
						this.status = "noMore";
					}
				}
			});

		},


		//消息列表
		toMsg() {
			uni.navigateTo({
				url: '/pages/msg/msg'
			})
		},
		//搜索跳转
		toSearch() {
			// 搜索推荐
			uni.request({
				url: 'http://shanpei.wsstreet.net/keyword', //仅为示例，并非真实接口地址。
				data: {
					token: this.token

				},
				method: "post",
				success: (res) => {

					console.log(res);
					this.searchList = res.data.data;
				}
			});

		},
		//轮播图跳转
		toSwiper(e, index) {

			//this.currentSwiper = index
		},
		//分类跳转
		toCategory(e) {

			uni.navigateTo({
				// url: '../goods/goods-list?cid=' + e.id + '&name=' + e.name
			});
		},
		//推荐商品跳转
		toPromotion(e) {
			uni.showToast({

			});
		},
		//商品跳转
		toGoods(e) {
			uni.showToast({

			});
			uni.navigateTo({

			});
		},
		//轮播图指示器
		swiperChange(event) {
			this.currentSwiper = event.detail.current;
		}
	}
}
